ied to a calendar cell when the cell's date is a special date(holiday, vacation, birthay. See the SpecialDates demo).*/\n.jqx-calendar-cell-specialDate\r\n{\r\n    color: #FF0000 !important;\r\n    background-color: transparent;\r\n    border: 1px solid #fff;\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n/*applied to a calendar cell when the cell's date is a restricted date.*/\n.jqx-calendar-cell-restrictedDate\r\n{\r\n    background-color: rgba(251, 132, 153, 1);\r\n    border: 1px solid #fff;\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n.jqx-calendar-cell-selected-invalid\r\n{\r\n    color: #FFF !important;\r\n    background: #FF0000 !important;\r\n    border: 1px solid #FF0000 !important;\r\n}\n/*applied to a calendar cell when the cell's date is the today date.*/\n.jqx-calendar-cell-today\r\n{\r\n    color: #898989;\r\n    background-color: #ffffdb;\r\n    border: 1px solid #fdc066;\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n/*applied to a calendar cell when the cell is hovered.*/\n.jqx-calendar-cell-hover\r\n{\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n/*applied to a calendar cell when the cell is selected.*/\n.jqx-calendar-cell-selected\r\n{\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n/*applied to a calendar cell when the cell's date represents a date from the previous or next month.*/\n.jqx-calendar-cell-othermonth\r\n{\r\n    color: #898989 !important;\r\n    margin: 0 0 0 0;\r\n    cursor: pointer;\r\n}\n/*applied to a calendar cell when the calendar is disabled.*/\n.jqx-calendar-cell-disabled\r\n{\r\n    color: #ccc;\r\n}\n/*applied to a calendar cell when the cell is from the row header that displays the week numbers.*/\n.jqx-calendar-row-cell\r\n{\r\n    outline: none;\r\n    text-align: center;\r\n    border: 1px solid transparent;\r\n    margin: 0 0 0 0;\r\n    cursor: default;\r\n}\n/*applied to a calendar cell when the cell is from the row header that displays the week numbers and the calendar is disab